export class FileStorageClient {
    constructor() {
        //  1:  Gateway    
        this.gatewayUrl = 'http://localhost:55699'; // Gateway
        this.apiPath = '/api/files';
    }

    /**
     *   URL   ( Gateway)
     * @param {string} endpoint 
     * @returns {string}
     */
    _getUrl(endpoint) {
        return `${this.gatewayUrl}${this.apiPath}${endpoint}`;
    }

    /**
     *    
     * @param {File} file -   
     * @returns {Promise<Object>}     URL
     */
    async UploadFile(file) {
        if (!(file instanceof File)) {
            throw new Error('    File');
        }

        const formData = new FormData();
        formData.append('file', file);

        try {
            //  2:  window.apiCall  fetch   
            const response = await window.apiCall(this._getUrl('/upload'), {
                method: 'POST',
                body: formData
                //   Content-Type     multipart/form-data  boundary
            });

            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(` : ${errorText}`);
            }

            return await response.json();
        } catch (error) {
            console.error('   :', error);
            throw error;
        }
    }

    /**
     *  URL      ( URL  Gateway)
     * @param {string} bucketName -  
     * @param {string} fileName -  
     * @returns {string}
     */
    GetFileViewUrl(bucketName, fileName) {
        return this._getUrl(`/view/${bucketName}/${fileName}`);
    }

    /**
     *  URL     ID ( URL  Gateway)
     * @param {number} id - ID 
     * @returns {string}
     */
    GetFileDownloadUrl(id) {
        return this._getUrl(`/download/${id}`);
    }

    /**
     *     ID
     * @param {number} id - ID 
     * @returns {Promise<Object>}
     */
    async GetFileMetadata(id) {
        try {
            const response = await window.apiCall(this._getUrl(`/${id}`), {
                method: 'GET',
                headers: { 'Content-Type': 'application/json' }
            });

            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(`  : ${errorText}`);
            }

            return await response.json();
        } catch (error) {
            console.error('   :', error);
            throw error;
        }
    }

    /**
     *    ID
     * @param {number} id - ID 
     * @param {string} saveAs -     ()
     */
    async DownloadFile(id, saveAs = null) {
        try {
            const response = await window.apiCall(this._getUrl(`/download/${id}`), {
                method: 'GET'
            });

            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(` : ${errorText}`);
            }

            const blob = await response.blob();

            let fileName = saveAs;
            if (!fileName) {
                const contentDisposition = response.headers.get('Content-Disposition');
                if (contentDisposition) {
                    const matches = /filename[^;=\n]*=((['"]).*?\2|[^;\n]*)/.exec(contentDisposition);
                    if (matches?.[1]) {
                        fileName = matches[1].replace(/['"]/g, '');
                    }
                }
            }
            fileName = fileName || `file-${id}`;

            const url = URL.createObjectURL(blob);
            const a = document.createElement('a');
            a.style.display = 'none';
            a.href = url;
            a.download = fileName;
            document.body.appendChild(a);
            a.click();

            URL.revokeObjectURL(url);
            document.body.removeChild(a);
        } catch (error) {
            console.error('   :', error);
            throw error;
        }
    }

    /**
     *  Blob 
     * @param {number} id - ID 
     * @returns {Promise<Blob>}
     */
    async GetFileBlob(id) {
        try {
            const response = await window.apiCall(this._getUrl(`/download/${id}`), {
                method: 'GET'
            });

            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(`  : ${errorText}`);
            }

            return await response.blob();
        } catch (error) {
            console.error('   blob:', error);
            throw error;
        }
    }

    /**
     *  URL   
     * @param {number} id - ID 
     * @returns {Promise<string>}
     */
    async GetFilePreviewUrl(id) {
        const blob = await this.GetFileBlob(id);
        return URL.createObjectURL(blob);
    }

    /**
     *    ID
     * @param {number} id - ID 
     * @returns {Promise<boolean>}
     */
    async DeleteFile(id) {
        try {
            const response = await window.apiCall(this._getUrl(`/${id}`), {
                method: 'DELETE'
            });

            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(` : ${errorText}`);
            }

            return true;
        } catch (error) {
            console.error('   :', error);
            throw error;
        }
    }

    /**
     *    
     * @param {File[]} files -  
     * @returns {Promise<Object[]>}
     */
    async UploadMultipleFiles(files) {
        const results = [];
        for (const file of files) {
            try {
                const result = await this.UploadFile(file);
                results.push({ success: true, fileName: file.name, data: result });
            } catch (error) {
                results.push({ success: false, fileName: file.name, error: error.message });
            }
        }
        return results;
    }

    /**
     *    
     * @param {File[]} files -  
     * @returns {Promise<Object[]>}
     */
    async UploadMultipleFilesParallel(files) {
        const uploadPromises = files.map(async (file) => {
            try {
                const result = await this.UploadFile(file);
                return { success: true, fileName: file.name, data: result };
            } catch (error) {
                return { success: false, fileName: file.name, error: error.message };
            }
        });

        return await Promise.all(uploadPromises);
    }

    /**
     *   URL ( ,    )
     * @param {string} newBaseUrl 
     */
    SetBaseUrl(newBaseUrl) {
        this.gatewayUrl = newBaseUrl.endsWith('/') ? newBaseUrl.slice(0, -1) : newBaseUrl;
    }

    /**
     *    URL
     * @returns {string}
     */
    GetBaseUrl() {
        return this.gatewayUrl;
    }
}

