export class NotificationClient {
    constructor() {
        this.gatewayUrl = 'http://localhost:55699'; // Gateway
        this.apiPath = '/api/notifications'; // lowercase   Gateway
    }

    /**
     *   URL   ( Gateway)
     * @param {string} endpoint 
     * @returns {string}
     */
    _getUrl(endpoint) {
        return `${this.gatewayUrl}${this.apiPath}${endpoint}`;
    }

    /**
     *   
     * @returns {Promise<Array>}
     */
    async GetAllNotifications() {
        try {
            const response = await window.apiCall(this._getUrl(''), {
                method: 'GET',
                headers: { 'Content-Type': 'application/json' }
            });

            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(`  : ${errorText}`);
            }

            return await response.json();
        } catch (error) {
            console.error('    :', error);
            throw error;
        }
    }

    /**
     *    ID
     * @param {string} id - GUID 
     * @returns {Promise<Object|null>}
     */
    async GetNotificationById(id) {
        try {
            const response = await window.apiCall(this._getUrl(`/${id}`), {
                method: 'GET',
                headers: { 'Content-Type': 'application/json' }
            });

            if (!response.ok) {
                if (response.status === 404) return null;
                const errorText = await response.text();
                throw new Error(`  : ${errorText}`);
            }

            return await response.json();
        } catch (error) {
            console.error('     ID:', error);
            throw error;
        }
    }

    /**
     *     
     * @param {string} userId - GUID 
     * @returns {Promise<Array>}
     */
    async GetNotificationsByUserId(userId) {
        try {
            const response = await window.apiCall(this._getUrl(`/user/${userId}`), {
                method: 'GET',
                headers: { 'Content-Type': 'application/json' }
            });

            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(`   : ${errorText}`);
            }

            return await response.json();
        } catch (error) {
            console.error('    :', error);
            throw error;
        }
    }

    /**
     *     
     * @param {string} userId
     * @returns {Promise<Array>}
     */
    async GetUnreadNotificationsByUserId(userId) {
        try {
            const response = await window.apiCall(this._getUrl(`/user/${userId}/unread`), {
                method: 'GET',
                headers: { 'Content-Type': 'application/json' }
            });

            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(`   : ${errorText}`);
            }

            return await response.json();
        } catch (error) {
            console.error('    :', error);
            throw error;
        }
    }

    /**
     *    
     * @param {string} userId
     * @returns {Promise<number>}
     */
    async GetUnreadCount(userId) {
        try {
            const response = await window.apiCall(this._getUrl(`/user/${userId}/unread/count`), {
                method: 'GET',
                headers: { 'Content-Type': 'application/json' }
            });

            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(`   : ${errorText}`);
            }

            return await response.json();
        } catch (error) {
            console.error('     :', error);
            throw error;
        }
    }

    /**
     *     ( ID  NotificationUsers)
     * @param {string} recipientId
     * @returns {Promise<boolean>}
     */
    async MarkAsReadByRecipientId(recipientId) {
        try {
            const response = await window.apiCall(this._getUrl(`/recipient/${recipientId}/mark-read`), {
                method: 'PATCH',
                headers: { 'Content-Type': 'application/json' }
            });

            if (!response.ok) {
                if (response.status === 404) return false;
                const errorText = await response.text();
                throw new Error(`    : ${errorText}`);
            }

            return true;
        } catch (error) {
            console.error('     :', error);
            throw error;
        }
    }

    /**
     *     ( ID   )
     * @param {string} notificationId
     * @param {string} userId
     * @returns {Promise<boolean>}
     */
    async MarkAsReadByIds(notificationId, userId) {
        try {
            const response = await window.apiCall(this._getUrl(`/notification/${notificationId}/user/${userId}/mark-read`), {
                method: 'PATCH',
                headers: { 'Content-Type': 'application/json' }
            });

            if (!response.ok) {
                if (response.status === 404) return false;
                const errorText = await response.text();
                throw new Error(`    : ${errorText}`);
            }

            return true;
        } catch (error) {
            console.error('      ( ID):', error);
            throw error;
        }
    }

    /**
     *      
     * @param {string} userId
     * @returns {Promise<number>}
     */
    async MarkAllAsRead(userId) {
        try {
            const response = await window.apiCall(this._getUrl(`/user/${userId}/mark-all-read`), {
                method: 'PATCH',
                headers: { 'Content-Type': 'application/json' }
            });

            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(`     : ${errorText}`);
            }

            const result = await response.json();
            //    { count: N }        
            if (typeof result === 'object' && result.count !== undefined) {
                return result.count;
            } else if (typeof result === 'string') {
                const match = result.match(/\d+/);
                return match ? parseInt(match[0], 10) : 0;
            }
            return 0;
        } catch (error) {
            console.error('      :', error);
            throw error;
        }
    }

    /**
     *   
     * @param {Object} notificationData
     * @returns {Promise<boolean>}
     */
    async CreateNotification(notificationData) {
        // 
        if (!notificationData.title) throw new Error(' title ');
        if (!notificationData.message) throw new Error(' message ');
        if (!notificationData.createdBy) throw new Error(' createdBy ');
        if (!Array.isArray(notificationData.userIdList) || notificationData.userIdList.length === 0) {
            throw new Error(' userIdList    ');
        }

        const payload = {
            title: notificationData.title,
            message: notificationData.message,
            imageUrl: notificationData.imageUrl || null,
            createdBy: notificationData.createdBy,
            type: notificationData.type === 0 ? 0 : 1,
            isReadable: notificationData.isReadable !== false, // true  
            userIdList: notificationData.userIdList
        };

        try {
            const response = await window.apiCall(this._getUrl(''), {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(payload)
            });

            if (!response.ok) {
                const errorText = await response.text();
                console.error('  :', errorText);
                return false;
            }

            return true;
        } catch (error) {
            console.error('   :', error);
            throw error;
        }
    }

    /**
     *   URL ( ,    )
     */
    SetBaseUrl(newBaseUrl) {
        this.gatewayUrl = newBaseUrl.endsWith('/') ? newBaseUrl.slice(0, -1) : newBaseUrl;
    }

    /**
     *    URL
     */
    GetBaseUrl() {
        return this.gatewayUrl;
    }
}

