export class EventClient {
    constructor() {
        this.gatewayUrl = 'http://localhost:55699'; // Gateway
        this.apiPath = '/api/events'; // lowercase    Gateway
    }

    /**
     *   URL   ( Gateway)
     * @param {string} endpoint 
     * @returns {string}
     */
    _getUrl(endpoint) {
        return `${this.gatewayUrl}${this.apiPath}${endpoint}`;
    }

    /**
     *   
     * @param {Object} eventData
     * @returns {Promise<Object>}   
     */
    async CreateEvent(eventData) {
        //   
        if (!eventData.title) throw new Error(' title ');
        if (!eventData.eventDateTime) throw new Error(' eventDateTime ');

        const payload = {
            title: eventData.title,
            description: eventData.description || null,
            imageUrl: eventData.imageUrl || null,
            eventDateTime: eventData.eventDateTime, // ISO 8601   Date
            place: eventData.place || null,
            communityId: eventData.communityId || null,
            communityType: eventData.communityType || 0 // 0 = House
        };

        try {
            const response = await window.apiCall(this._getUrl(''), {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(payload)
            });

            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(`  : ${errorText}`);
            }

            return await response.json();
        } catch (error) {
            console.error('   :', error);
            throw error;
        }
    }

    /**
     *    ID
     * @param {number} eventId
     * @returns {Promise<Object|null>}
     */
    async GetEventById(eventId) {
        try {
            const response = await window.apiCall(this._getUrl(`/${eventId}`), {
                method: 'GET',
                headers: { 'Content-Type': 'application/json' }
            });

            if (!response.ok) {
                if (response.status === 404) return null;
                const errorText = await response.text();
                throw new Error(`  : ${errorText}`);
            }

            return await response.json();
        } catch (error) {
            console.error('     ID:', error);
            throw error;
        }
    }

    /**
     *     
     * @returns {Promise<Array>}
     */
    async GetUserEvents() {
        try {
            const response = await window.apiCall(this._getUrl(''), {
                method: 'GET',
                headers: { 'Content-Type': 'application/json' }
            });

            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(`   : ${errorText}`);
            }

            return await response.json();
        } catch (error) {
            console.error('    :', error);
            throw error;
        }
    }

    /**
     *   
     * @param {number} eventId
     * @returns {Promise<boolean>}
     */
    async SubscribeToEvent(eventId) {
        try {
            const response = await window.apiCall(this._getUrl(`/${eventId}/subscribe`), {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' }
            });

            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(`   : ${errorText}`);
            }

            return true;
        } catch (error) {
            console.error('    :', error);
            throw error;
        }
    }

    /**
     *   
     * @param {number} eventId
     * @returns {Promise<boolean>}
     */
    async UnsubscribeFromEvent(eventId) {
        try {
            const response = await window.apiCall(this._getUrl(`/${eventId}/unsubscribe`), {
                method: 'DELETE',
                headers: { 'Content-Type': 'application/json' }
            });

            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(`   : ${errorText}`);
            }

            return true;
        } catch (error) {
            console.error('    :', error);
            throw error;
        }
    }

    /**
     *   URL Gateway ( )
     */
    SetBaseUrl(newBaseUrl) {
        this.gatewayUrl = newBaseUrl.endsWith('/') ? newBaseUrl.slice(0, -1) : newBaseUrl;
    }

    /**
     *    URL
     */
    GetBaseUrl() {
        return this.gatewayUrl;
    }
}